import React from "react";
import { Link, useHistory } from "react-router-dom";

import { useGlobalContext } from "./Providers/GlobalProvider.js";

import { auth } from "./Firebase/Firebase.js";

import "./styles/Nav.css";

const IMG_Bartender = require("./images/bartender.png").default;

const Nav = () => {
  const history = useHistory();

  const { currentUser, setUserID, setFavDrinks } = useGlobalContext();

  const logoutUser = async () => {
    try {
      await auth.signOut();
      setFavDrinks([]);
      setUserID(null);
      history.push("Cocktails/page/1");
    } catch (error) {
      console.log(error);
    }
  };

  return (
    <div className="navbar">
      <div className="nav-image">
        <img src={IMG_Bartender} alt="bartender"></img>
      </div>
      <div className="nav-links">
        <Link to="Cocktails/page/1">Home</Link>
        <Link to="Cocktails/About">About</Link>
        <Link to="Cocktails/Favourites">Favourites</Link>
        {currentUser ? (
          <button onClick={logoutUser}>Sign Out</button>
        ) : (
          <Link to="/SignIn">Login</Link>
        )}
      </div>
    </div>
  );
};

export default Nav;
